
body{
  background:lightpink;
  opacity: 0.8;

}
.bbt{
  background: blue;
  opacity:1;
 
  
}

.col-12{
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 20px;
}

#whole{

 width: 500px;
  height: 350px;
  position: absolute;
	top:0;
	bottom: 0;
	left: 0;
	right: 0;
  margin: auto;
  background: lightgrey;
	border:1px solid red;
  padding:15px;
  border-radius: 4%;

  
}
.aftergooglelogin{
  margin-top: 15px;
 
}
#cr7{
  position:relative;
  top: 0;
  left:20%;
  opacity: 0.8;


}
#lastcontent{
  position: relative;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  top: 0px;
  left: 2%;
  text-align: center;
  
  
}
.content{
  text-align: center;
}

.btnFacebook { 
  width: 250px;
  height:35px;  
  border-radius: 4px;
  background: #3b5998;
  color:white;
  border:0px transparent;  
  text-align: center;
  margin:5px;
  display:-moz-box;
}


.btnGoogle { 
  margin:3px;
  width: 250px;
  height:35px;
  border-radius: 4px;
  background: #db3236;
  color:white;
  border:0px transparent;
  text-align: center;
}
.btntwitter{
  margin:3px;
  width: 250px;
  height:35px;
  border-radius: 4px;
  background: #3075dd;
  color:white;
  border:0px transparent;
  text-align: center;

}
.btnFacebook:hover {
  background: #3b5998;
  opacity: 0.6;
}
.btnGoogle:hover {
  background: #db3236;
  opacity: 0.6;
}

@media only screen and (max-width: 600px) {
  #lastcontent{
    text-align: center;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
  } 
  #cr7{
    position:relative;
    top: 0;
    left:20%;
    opacity: 0.8;
  }
  #whole{
    width: 500px;
    height: 350px;
    position: absolute;
    top: 100px;
    left: 60px;
  }

}
